.app{
  --bg-color-1:#d1db77;
  --bg-color-2:#fcfcfc;
  --text-color-1:#0b1537;
  --text-color-2:#636363;

}
.app[data-theme="dark"]{
  --bg-color-1:#a3a107;
  --bg-color-2:#868400;
  --text-color-1:#fbfcfc;
  --text-color-2:#bbbcbc;

}
.custom-scroll::-webkit-scrollbar{
  height: 5px;
  width: 5px;
}
.custom-scroll::-webkit-scrollbar-track{
  background-color: transparent;
}
.custom-scroll::-webkit-scrollbar-thumb{
  background-color: rgba(0,0,0,0.14);
}
.custom-scroll::-webkit-scrollbar-thumb:hover{
  background-color: gray;
}
.app{
  background-color: #b8b500;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items:center;
}
.app_calculator{
  height: 500px;
  width: 280px;
  border-radius: 30px;
  box-shadow: 1px -1px 20px rgba(0,0,0,0.15);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.app_calculator_navbar{
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  width: 100%;
  background-color: transparent;
  min-height: 40px;
  padding: 10px;
  display: flex;
  gap: 10px;
  align-items: center;
}
.app_calculator_navbar img { 
  height: 25px;
  width: 25px;
}
.app_calculator_navbar_toggle{
  height: 15px;
  width: 30px;
  border-radius: 20px;
  box-shadow: 1px 1px 40px 80px rgba(0,0,0,0.14);
}
.app_calculator_navbar_toggle_circle{
  height: 15px;
  width: 15px;
  border-radius: 50%;
  background-color: #fff;
  transition: 300ms;
}
.app_calculator_navbar_toggle_circle_active{
  margin-left: 15px;
}